.navigation{align-items:center;display:flex;justify-content:space-around;min-height:8vh;position:sticky}.navigation__logo{text-transform:uppercase;letter-spacing:5px;font-size:20px}.navigation__logo a{color:black;text-decoration:none}.navigation__links-wrapper{display:flex;justify-content:space-around;list-style:none;width:30%}@media (max-width: 1024px){.navigation__links-wrapper{width:50%}}@media (max-width: 768px){.navigation__links-wrapper{background-color:rgba(234,231,220,0.6);position:absolute;right:0px;height:100vh;top:8vh;align-items:center;display:flex;justify-content:center;flex-direction:column;width:40%;-webkit-transform:translateX(100%);transform:translateX(100%);transition:-webkit-transform 0.5s ease-in;transition:transform 0.5s ease-in;transition:transform 0.5s ease-in, -webkit-transform 0.5s ease-in}}.navigation__links-wrapper.--active{justify-content:space-evenly;-webkit-transform:translateX(0%);transform:translateX(0%)}.navigation__links-wrapper a{text-decoration:none}.navigation__links-wrapper a:hover{background-color:#d8c3a5}.navigation__link{color:black;font-weight:bold;font-size:14px;letter-spacing:3px;padding:0.5em}@media (max-width: 768px){.navigation__link{opacity:0}}.navigation__mobile-nav{display:none;cursor:pointer}@media (max-width: 768px){.navigation__mobile-nav{display:block}}.navigation__mobile-nav div{background-color:black;width:25px;height:3px;margin:5px;transition:all 0.3s ease}.navigation__mobile-nav.--toggle .line1{-webkit-transform:rotate(-45deg) translate(-5px, 6px);transform:rotate(-45deg) translate(-5px, 6px)}.navigation__mobile-nav.--toggle .line2{opacity:0}.navigation__mobile-nav.--toggle .line3{-webkit-transform:rotate(45deg) translate(-5px, -6px);transform:rotate(45deg) translate(-5px, -6px)}@-webkit-keyframes navigationLinkFade{from{opacity:0;-webkit-transform:translateX(50px);transform:translateX(50px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes navigationLinkFade{from{opacity:0;-webkit-transform:translateX(50px);transform:translateX(50px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}.counter-wrapper{align-items:center;display:flex}.counter-wrapper__counter{background:#e85a4f;border-radius:50%;color:black;font-weight:600;text-align:center;margin:0 5px;width:4vh}

.home-wrapper{height:92vh;background-image:url(/shopping-cart/static/media/background-large.7e4acaf7.jpg);background-position:center center;background-repeat:no-repeat;background-attachment:fixed;background-size:cover;background-color:#eae7dc}@media (max-width: 768px){.home-wrapper{height:100vh}}.page-intro{align-items:flex-start;display:flex;flex-direction:column;left:2%;position:relative;top:20%}@media (max-width: 768px){.page-intro{top:0%}}@media (max-width: 1024px){.page-intro{top:0%}}.page-intro__header{font-size:45px;max-width:250px}@media (max-width: 768px){.page-intro__header{max-width:100%}}@media (max-width: 1024px){.page-intro__header{max-width:100%}}.page-intro__shop-btn{background:rgba(234,231,220,0.580392);border:none;color:black;cursor:pointer;font-size:20px;font-weight:bold;letter-spacing:3px;padding:0.5em}.page-intro__shop-btn:hover{background:#bdbaad}

.card{align-items:center;background:#d8c3a5;border:4px solid #8e8d8a;border-radius:5px;display:flex;flex-direction:column;justify-content:space-between;text-align:center;padding:2em}.card__sale-price{color:#b51205}.card__button{background:#e98074;border:none;border-radius:5px;cursor:pointer;font-size:1em;font-weight:900;padding:1em;margin-top:1em}.card__button:hover{background:#e85a4f}.card__button:disabled{background:#b7817a}.card__name{display:flex;flex-direction:column;text-align:center;min-height:7rem;max-height:10rem;padding-top:10%}.card__regular-price{margin-top:1em}

.shop-wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;grid-row-gap:2rem;row-gap:2rem;padding:2rem;height:92vh;overflow-y:auto}

.warning-message{color:#e85a4f;display:flex;flex-direction:column;font-size:50px;font-weight:bold;margin-top:18%;text-align:center}.warning-message__shop-btn{background:#d8c3a5;border:none;color:black;cursor:pointer;font-size:20px;font-weight:bold;letter-spacing:3px;padding:0.5em}.warning-message__shop-btn:hover{background:#b59a74}.warning-message__link{cursor:unset}.cart-page{align-items:center;display:flex;flex-direction:column}.cart-wrapper{align-self:center;border:2px solid #8e8d8a;margin:2rem 1rem;text-align:center;padding:1rem}.cart-wrapper__header{color:#e85a4f;margin-bottom:1em}.cart-wrapper__content{display:flex;flex-direction:column}.order-info{align-items:center;display:flex;flex-direction:column}.order-info__total-price{font-size:30px;font-weight:bold}.order-info__purchase-btn{background:#d8c3a5;border:none;cursor:pointer;font-size:30px;margin:0.5em;padding:5px 30px}.order-info__purchase-btn:hover{background:#b59a74}.product-card{align-items:center;display:flex;justify-content:space-around;align-self:center;border:1px solid #8e8d8a;font-size:20px;margin:1em 0;padding:2rem 0;max-width:800px}@media (max-width: 768px){.product-card{max-width:500px}}.product-card>*{margin:0 1em}.product-card__name{width:300px}.product-card__price{align-items:flex-end;display:flex;flex-direction:column}.product-card__quantity{align-items:center;display:flex;flex-direction:column;width:7rem}.product-card__quantity__button-wrapper{display:flex;justify-content:space-evenly;width:100%}.product-card__quantity__btn{background:#d8c3a5;border:none;cursor:pointer;font-weight:bold;width:30px}.product-card__quantity__btn:hover{background:#b59a74}

*{box-sizing:border-box;font-family:"Poppins",sans-serif;margin:0px;padding:0px}body{background-color:#eae7dc;overflow-x:hidden}

